const initialState = {
    onCart: [false, false, false, false, false, false, false, false],
    total: 0,
    items:0,
};

function addCart(state = initialState, action) {
    switch (action.type) {
        case "ADD_TO_CART":
                const newStore = state;
                if (newStore.onCart != undefined) {

                    newStore.onCart[action.payload] = !(newStore.onCart[action.payload]);
                }
                return {
                    ...newStore
                };

        case "REMOVE_FROM_CART":
            return {
                ...state, ...action.payload
            }
        case "ADD_VALUE":
            const newState = state;
            newState.total += action.payload
            newState.items++;
            return { ...newState };
        case "REMOVE_VALUE":
            const newState1 = state;
            newState1.total -= action.payload
            newState1.items--;
            return { ...newState1 };

        default:
            return state;
    }
    
}
export default addCart;