import React from "react";
import { Redirect } from "react-router-dom";
import { TextField,makeStyles, Card ,CardContent, CardActionArea, Typography} from "@material-ui/core";
const useStyles = makeStyles({
    root: {
        minWidth:300,
        maxWidth: 355,
        padding: "10px",
        margin: "20px  auto auto 20px",
        textAlign:"left",
      },
});
function valueChange(e) {
    localStorage['address'] =e.target.value
}

function CheckoutForm({props}) {
    const classes = useStyles();
    if(localStorage['login'] != "true"){
        return <Redirect to="/signup" /> 
    }
    return (
           
        <Card className={classes.root}>
            <CardContent>
            <TextField
				fullWidth
				type="text"
                label="name"
                className="m-2"
                id="name"
                
                size="small"
                defaultValue={props.Name}
                variant="outlined"
                InputProps={{
                    readOnly:true,
                }}
				name="name" />
			<br />
            <TextField
                fullWidth
                className="m-2"
				type="email"
				label="email"
                id="email"
                size="small"
                defaultValue={props.Email}
                variant="outlined"
                InputProps={{
                    readOnly:true,
                }}
				name="email" />
            <br />
            <TextField
                fullWidth
                className="m-2"
				type="number"
				label="Mobile No"
                id="mob-no"
                size="small"
                defaultValue={props.PhoneNo}
                variant="outlined"
                InputProps={{
                    readOnly:true,
                }}
				name="mobile-no" />
                <br />
            <TextField
                fullWidth
                className="m-2"
				type="text"
                label="Addresss"
                multiline
                id="address"
                required
                size="small"
               onChange={valueChange}
				name="Address" />
			<br />
            </CardContent>
        
  </Card>

    )


}

export default CheckoutForm;