import React, {useState} from "react";
import Navbar from "./Navbar";
import store from "../store/index";
import { Button } from "@material-ui/core"
import Data from "../Data/products_data";
import { Product } from "./Product";
import { Redirect } from "react-router-dom";
import { useDispatch } from "react-redux";
import { addValue } from "../actions/index";
function Cart() {
    const dispatch = useDispatch();
    const [check, onCheck] = useState();
    const values = [2999,6199,4234,6999,7399,2499,2600,4300]
    const onCart = store.getState().addCart.onCart;
    console.log(onCart);
    onCart.map((val,index) => {
        if (val) {
            dispatch(addValue(values[index-1]));
        }
    })
    function onCheckout(total_items, total) {
        console.log("Redirec");
        onCheck(true);
    }

    function ProductsOnCart() {
        return (
            Data.map((val) => {
                if (onCart[val.id]) {
                    // console.log("S");
                    return <Product data={val} home={false} pm={true} key={val.id} />
            }
            })
        )
    }
    if (check) {
        return <Redirect to={{ pathname:"/Checkout"}} />
    }
    if (localStorage['login'] != "true") {
        return <Redirect to="/signup" />
    }

    return (
        <div className="cart">
            <Navbar icon="home" total={"12"}/>
            <div className="w-100 mt-2 row align-items-center">
                <Button size="large"onClick={() => onCheckout() } variant="contained" color="secondary" className="m-auto "
             >CheckOut</Button>
            </div>
            {ProductsOnCart()}
        </div>
    )
}

export default Cart;